<template>
  <div v-loading='loading'>
     <cms-breadcrumb to="/store/category" :type='$route.query.type'  :params='{id:$route.query.storeId}'>
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px"  @submit.native.prevent>
        <el-form-item label="分类名称：" prop="cateName" :error='errorMessage.cateName'>
          <el-input v-model="formData.cateName" maxlength="50"></el-input>
        </el-form-item>
         <el-form-item label="上级分类：" prop="parentId">
         <el-cascader :options="options" :props="props" :change-on-select='true' v-model="formData.parentIds" :disabled="true">
          </el-cascader>
          <div class="gray">不选择，默认为顶级分类</div>
        </el-form-item>
         <el-form-item label="是否显示：" prop="isShow" :error="errorMessage.isShow">
          <cms-switch v-model="formData.isShow"></cms-switch>
        </el-form-item>
        <el-form-item label="是否显示店铺导航栏" prop="isTop" :error="errorMessage.isTop">
          <cms-switch v-model="formData.isTop"></cms-switch>
          <span class="gray">设置该分类是否显示在店铺分类导航中</span>
        </el-form-item>
        <el-form-item label="分类页标题：" prop="title">
          <el-input v-model="formData.title" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="分类页关键字：" prop="keywords">
          <el-input v-model="formData.keywords" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="分类页描述：" prop="description">
          <el-input type='textarea' :rows='4' v-model="formData.description" maxlength="200"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
         <cms-button  @click="update(true)" v-color >修改</cms-button>
        <cms-button type='rest' @click="getAll">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { categoryGet, storeCategoryTree, categoryUpdate } from '@/api/shop/shopManage.js'
var defaultData = {
  'id': '',
  'cateName': '',
  'parentId': '',
  'isShow': '',
  'isTop': '',
  'title': '',
  'keywords': '',
  'description': '',
  'parentIds': []
}
export default {
  mixins: [form],
  data() {
    return {
      loading: false,
      options: [],
      props: {
        value: 'id',
        label: 'cateName',
        children: 'childs',
        disabled: 'isAuth'
      },
      formData: Object.assign({}, defaultData),
      rules: {
        cateName: [rules.required('请填写分类名称')],
        isShow: [rules.required()],
        isTop: [rules.required()]
      }
    }
  },
  methods: {
    getFormData(id) { // 获取分类信息
      categoryGet({ id: id }).then(res => {
        this.formData = res.data
        const nodeIds = this.$cms.arrayToNumber(this.$route.query.nodeIds)
        if (nodeIds.length <= 1) { // 判断父级传过来nodeIds的长度
          this.formData.parentIds = [0]// 为空或只有一层则表示顶级分类
        } else {
          nodeIds.pop()// 删除本身节点信息
          this.formData.parentIds = nodeIds// 显示所有父级分类
        }
      })
    },
    getMenuTree() {
      // 获取菜单节点
      storeCategoryTree({ storeId: this.$route.query.storeId }).then(res => {
        const root = [{ id: 0, cateName: '顶级分类' }]
        this.options = root.concat(res.data)
      })
    },
    update() {
      const params = this.$cms.deepCopy(this.formData)
      // params.parentId = params.parentIds[params.parentIds.length - 1]
      delete params.parentIds// 处理掉参数
      this.updateForm(categoryUpdate, params, '/store/category', 'list')
    },
    getAll() {
      this.getFormData(this.$route.query.id)
      this.getMenuTree()
    }
  },
  created() {
    this.formData.id = this.$route.query.id
    this.formData.storeId = this.$route.query.storeId
    this.getAll()
  }

}
</script>




